import back from '@/components/back'
import noData from '@/components/noData'

export default {
    components: { noData, back },
    data(){
        return {
            list: [],
            page: 1,
            Patient: this.$store.state.patient,
            user: this.$store.state.userInfo.Data,
            haveMore: true,
        }
    },
    created(){
        this.pull(false)
    },
    mounted(){
        this.getMore()
    },
    beforeDestroy(){
        window.onscroll = null
    },
    methods: {
        pull(h){
            if(this.haveMore){
                this.$post('Prescription/Prescription/GetPrescriptionListByDoctorId',{
                    PatientId:this.Patient.PatientId,
					DoctorId:this.user.User.DoctorId,
                    PageIndex: this.page,
                    PageSize: 15,
                }).then(o => {
                    if (!o.ReturnData || o.ReturnData.length < 15){
                            this.haveMore = false
                        }
                        else {
                            this.haveMore = true
                        }
                        
                        if (h){
                            o.ReturnData.forEach(v => {
                                this.list.push(v)
                            })
                        }
                        else {
                            this.list = o.ReturnData
                        }
                })
            }
        },
        toPre(i){
            this.$post('Prescription/Pharmacy/SetMyMedicationCompany',{
                MedicationCompanyId: i.MedicationCompanyId,
                DoctorId: this.user.User.DoctorId,
            }).then(o => {
                this.$store.dispatch('pushpreNo',null)
				this.$store.dispatch('pushpullFlag', 0)
                this.$store.dispatch('pushDiagnoses2', i.Diagnoses)
                this.$store.dispatch('pushupList', i.ListDetails)
                this.$router.push(`/prescription?rejectId=${i.Id}`)
            })
        },
        getMore(){
            const el = this.$refs.el
            let timer = null
            window.onscroll = () => {
                clearTimeout(timer)
                timer = setTimeout(() => {
                    const top = document.documentElement.scrollTop
                                || window.pageYOffset
                                || document.body.scrollTop,
                            h = el.scrollHeight,
                            wh = window.innerHeight
                    if (h - top - wh < 100 && this.haveMore){
                        this.page += 1
                        this.pull(true)
                    }
                }, 100)
            }
        }
    }
}



